
$figure-scale: 0.33;
$figure-width: 140px * $figure-scale;
$figure-height: 200px * $figure-scale;

.countdown {
  position: absolute;
  top: 0;
  bottom: 0;
  left: 5px;
  right: 0;
  margin: auto;
  margin-left: 0;
  width: $figure-width * 2;
  height: $figure-height;
  text-align: center;

  .figure {
    position: relative;
    display: inline-block;
    width: $figure-width;
    height: $figure-height;
    font: $figure-width "Roboto Slab", sans-serif;
    color: #404040;
    background: rgba(90, 90, 90, 1);
    border: solid 3px #404040;
    box-sizing: border-box;
    border-radius: 18px;

    .base span {
      position: absolute;
      display: block;
      width: 100%;
    }
    
    .upper {
      position: absolute;
      width: 100%;
      height: 50%;
      background: linear-gradient(
        0deg,
        rgba(200, 200, 200, 1),
        rgba(255, 255, 255, 1) 15%
      );
      border-radius: 15px 15px 0 0;
      box-shadow: inset 0 1px 8px rgba(0, 0, 0, 0.1);
      overflow: hidden;
      border-bottom: solid 1px rgba(0, 0, 0, 0.5);
      box-sizing: border-box;
      transform-origin: 50% 100%;
      -webkit-transform-origin: 50% 100%;
    }

    .b-upper {
      z-index: 1;
    }

    .f-upper {
      z-index: 3;
    }

    .f-lower {
      z-index: 4;
    }
    
    .b-lower {
      z-index: 2;
    }

    .lower {
      position: absolute;
      top: 50%;
      width: 100%;
      height: 50%;
      background: linear-gradient(
        180deg,
        rgba(200, 200, 200, 1),
        rgba(255, 255, 255, 1) 15%
      );
      border-radius: 0 0 15px 15px;
      box-shadow: inset 0 -3px 5px rgba(0, 0, 0, 0.2);
      overflow: hidden;
      border-top: solid 1px #ddd;
      box-sizing: border-box;
      -webkit-transform-origin: 50% 0;
      transform-origin: 50% 0;
    }
    
    .lower span {
      position: relative;
      top: -100%;
    }  
  }

  .colon {
    position: relative;
    display: inline-block;
    font: $figure-width "Roboto Slab", sans-serif;
    top: -78px;
    left: $figure-width + 4px;
    color: #404040;
  }
}
